<template>
    <md-list-item class="col-12 no-padding-left">
        <md-card class="col-12 no-padding-left">
            <md-card-header>

                <div class="row">
                    <div class="col-6">
                        <md-avatar>
                            <img :src="img_url" alt="People">
                        </md-avatar>

                        <span class="md-list-item-text">Mary Johnson</span>
                        <br>
                        <span>UEM</span>
                        <br>
                        <span>20pt</span>
                    </div>

                    <div class="col-6 no-padding-right">
                        <md-card-expand class="pull-right">
                            <md-card-actions class="pull-right" md-alignment="space-between">
                                <div>
                                    <md-card-expand-trigger>
                                        <md-button>Togle</md-button>
                                    </md-card-expand-trigger>

                                    <!--<a v-b-popover.hover.left="'I start <strong>open</strong>'" title="Bom Explicador">-->
                                        <!--<span><i class="fa fa-2x fa-eye"></i> </span>-->
                                    <!--</a>-->

                                    <a id="popover" variant="primary"
                                       ref="button">
                                        <span><i class="fa fa-2x fa-eye"></i> </span>
                                    </a>
                                    <!--Por over-->

                                    <b-popover ref="popover" target="popover" triggers="hover focus" title="Explicadores 2018">
                                        Jose  <strong>Seie</strong>
                                        <br>
                                        <div>
                                            <h6>Estudante da UEM</h6>
                                            <b-table striped hover :items="items"></b-table>
                                            <br>
                                            <br>
                                            <b-btn @click="onClose" size="sm" variant="danger">Cancel</b-btn>
                                            <b-btn @click.prevent="" size="sm" variant="primary">Ok</b-btn>
                                        </div>
                                    </b-popover>
                                    <!--Pop over fim-->

                                </div>
                            </md-card-actions>

                            <md-card-expand-content>
                                <md-card-content>

                                    <explicador-subject></explicador-subject>
                                    <explicador-subject></explicador-subject>
                                    <explicador-subject></explicador-subject>
                                    <explicador-subject></explicador-subject>

                                </md-card-content>
                            </md-card-expand-content>
                        </md-card-expand>
                    </div>
                </div>

            </md-card-header>



        <!--Dialog  de alert sobre o user nao ter disciplinas-->
        <md-dialog id="dialog-profile" ref="dialog_message">
            <!--<md-dialog-title>Oi amigo</md-dialog-title>-->

            <md-dialog-content>
                <h5 ref="nome-exp"></h5>
                <p>Deseja enviar-lhe uma mensagem perguntando que disciplinas ele explica??</p>

            </md-dialog-content>

            <!--<md-dialog-action>-->
            <md-button class="md-primary button-remove pull-right" @click="closeDialog('dialog_message')">Fechar</md-button>
            <!--</md-dialog-action>-->
        </md-dialog>
        <!--FIm da dialog-->
        </md-card>
    </md-list-item>
</template>
<script>
    import popOverAdvanced from './PopOverAdvanced.vue'
    import explicadorSubject from './ExplicadorSubject.vue'
    import MdCardExpand from "../../../node_modules/vue-material/src/components/MdCard/MdCardExpand/MdCardExpand.vue";
    import MdCardContent from "../../../node_modules/vue-material/src/components/MdCard/MdCardContent/MdCardContent.vue";
    const items = [
        { age: 40, first_name: 'Dickerson', last_name: 'Macdonald' },
        { age: 21, first_name: 'Larsen', last_name: 'Shaw' },
        { age: 89, first_name: 'Geneva', last_name: 'Wilson' },
        { age: 38, first_name: 'Jami', last_name: 'Carney' }
    ]
    export default {
        components: {
            MdCardContent,
            MdCardExpand,
            explicadorSubject,
            popOverAdvanced},
        props: ['img_url','name'],
        data () {
            return {
                amount: 30,
                popoverShow: false,
                onOk: true,
                items: items
            }
        },
        methods: {
            openDialog(ref) {

                this.$refs['nome-exp'].innerHTML = 'O Explicador ainda nao registou as suas disciplinas';
                this.$refs['dialog_message'].open();

            },
            onClose () {
                this.popoverShow = false;
            },
            closeDialog(ref) {
                this.$refs[ref].close();
            }
        }
    }
</script>

<style >
    .no-padding-left, .md-list-item-default,.md-list-item-content {
        padding-left: 0;
    }
    .no-padding-right {
        padding-right: 0;
    }
    .md-list-item-default {
        padding-right: 0;
    }
    .md-card-header {
        padding-bottom: 0px;
    }
    .iniciais-discipla {

        width: 40px;
        height: 40px;
        margin-bottom: 10px;
        color: white;
        margin-right: 15px;
        border-radius: 5px;
        font-weight: bold;
        /*background: #3f51b5;*/
        background: orange;

    }
    .md-card {
        width: 100%;
        margin: 4px;
        display: inline-block;
        vertical-align: top;
    }

    .color-orange{
        color: orange!important;
    }
    .color-black{
        color: black!important;
    }
    .pointer {
        cursor: pointer;
    }
    img {
        width: 40px;
        margin: auto;
        display: block;
        margin-bottom: 10px;
    }

    .md-dialog {
        min-width: 280px;
        max-width: 40%;
    }
    .md-dialog-title {
        margin-bottom: 10px;
        padding: 20px 24px 0;
    }
    .md-list.md-triple-line .md-list-item .md-list-item-container {
        min-height: 60px !important;
    }

    .md-card-content {
        margin-top: 70px;
        border-top: solid #ffc107 2px;
    }

</style>


