<template>

    <div>

        <md-list-item>
            <div :title="subject.name" class="iniciais-discipla text-center">
                <h4>{{ sigla }}</h4>
            </div>

            <div class="md-list-text-container">
                <p class="verde-text mt-0 mb-0">{{ subject.name }} </p>
                <h5 class="verde-text">{{ subject.points }} pontos</h5>
                <!--<h5 v-else="" class="verde-text">0 pontos</h5>-->
            </div>

            <a href="#" @click.prevent="openDialog('dialog_message')" class="b_btn">Solicitar explicação</a>
            <md-divider class="md-inset"></md-divider>
        </md-list-item>

        <!--Dialog  de alert sobre o user nao ter disciplinas-->
        <md-dialog id="dialog-profile" ref="dialog_message">
            <!--<md-dialog-title>Oi amigo</md-dialog-title>-->

            <md-dialog-content>
                <h5 ref="nome-exp"></h5>
                <!--<explicador_form_message :userauth="userauth" :explicador="explicador" :subject="subject"></explicador_form_message>-->

            </md-dialog-content>

            <!--<md-dialog-action>-->
            <md-button class="md-primary button-remove pull-right" @click="closeDialog('dialog_message')">Fechar</md-button>
            <!--</md-dialog-action>-->
        </md-dialog>
        <!--FIm da dialog-->

    </div>




</template>

<script>

//    import explicador_form_message from './explicador-form-message.vue'
    export default {

        props: ['subjects','explicador'],
        data () {
            return {
                subject: {
                    id: 20,
                    name: 'Matematica discreta I',
                    points: 25,
                },
            }
        },
        computed: {

            isAuth() {
                return this.$store.state.auth
            },
            sigla()  {
                return this.subject.name.split(" ")[0].charAt(0) + ''+this.subject.name.split(" ")[this.subject.name.split(" ").length -1].charAt(0).toUpperCase()
            }
        },
        methods: {
            openDialog(ref) {

                this.$refs['nome-exp'].innerHTML = 'Confirme o envio do seu pedido, escreva uma mensagem para ele se desejar';
                this.$refs['dialog_message'].open();

            },
            closeDialog(ref) {
                this.$refs[ref].close();
            }
        }

    }

</script>


